<template lang="pug">

transition(name="slide-fade" mode="out-in")
	.pde(v-if="!$store.state.loading")
		fieldset.field-a.container-card(v-for="(a,index) in planExp")
			legend.bg--main.text-pure-paper.box-shadow-S {{anios[index-1]}}
			table.p
					tr.h
						th.i.text-ink70(rowspan=2) N°
						th.n.text-ink70(rowspan=2) Asignatura
						th.c2.text-ink70(colspan=2) Correlativas
					tr
						th.c.text-ink70 Débiles
						th.c.text-ink70 Fuertes
			fieldset.field-s(v-for="(s,index2) in a")
				legend.bg-pure-paper.text-main {{sems[index2]}}
				table.p
					Asignatura(v-for="(m,index3) in s" :i="parseInt(index3)" :m="m")


</template>
<script>
import axios from 'axios'
import Asignatura from '@/components/Asignatura.vue'

export default {
  name: 'Plan',
  data() {
  	return {
  		plan: {},
  		planExp: {},
  		loading: true,
  		anios: ['Primer año','Segundo año','Tercer año','Cuarto año','Quinto año','Sexto año','Séptimo año'],
  		sems: { 1: 'Primer semestre', 2: 'Segundo semestre', 'Anual': 'Anual' }, 
  	}
  },
  components: {
    Asignatura
  },
  props: {
  	carrera: String,
  	facultad: String
  },
  methods:{

  	explodePlan: function(plan){

  		var tmp = {};
		Object.keys(plan).forEach(function(key) {

			var a = plan[key].a;
			var s = plan[key].s;
			var o = plan[key];

			if(tmp[a] == undefined) tmp[a] = {};
			if(tmp[a][s] == undefined) tmp[a][s] = {};
			
			tmp[a][s][key] = {'n': o.n, 'cd': {}, 'cf': {}}

			Object.keys(o.cd).forEach(function(key2) {
				var c = o.cd[key2];
				tmp[a][s][key].cd[key2] = {'i': c, 'n': plan[c-1].n};
			});

			Object.keys(o.cf).forEach(function(key3) {
				var c = o.cf[key3];
				tmp[a][s][key].cf[key3] = {'i': c, 'n': plan[c-1].n};
			});


		});
		this.$store.commit('stopLoading')
		return(tmp)



  	}

  },
  beforeMount(){
    this.$store.commit('startLoading')
  },
  mounted() {
  	axios
  		.get("/assets/json/planes/"+this.facultad+"_"+this.carrera+".json")
  		.then(response => (
  			this.plan = response.data,
  			this.planExp = this.explodePlan(response.data)
  			)
  		)
  }
}

</script>
<style lang="stylus">

@import "../assets/styles/variables.styl";

.field-a, .field-s

	legend
		font-size: 12px
		font-weight: bold
		border-radius: 4px
		text-transform: uppercase

	padding: 0 8px
	
	

.field-a

	legend
		padding:4px 10px
		//background: color-main
		//color: color-bg	

	margin: 0 -12px 12px
	//border: 2px color-main solid
	border: none
	border-radius: 6px


.field-s 

	legend
		padding:2px 8px

	margin: 0 -8px 6px
	border: none
	border-top: 1px alpha(color-main, 10%) solid
	

table.p
	th
		vertical-align:middle
		font-size:12px
		text-transform: uppercase
	td
		padding: 6px 0
	.i
		width: 8%
		text-align:center
		font-weight:bold
	.n
		width: 52%
	.c
		.r
			font-weight:bold
			padding: 0 4px
		width: 20%
		text-align:center
	.c2
		width: 40%
		text-align:center
	
	width:100%

</style>