<template lang="pug">

#plan
	Title(v-if="this.$route.params.titulo === undefined" :t="carrera.replace(/([A-Z])/g, ' $1').trim()" s="Plan de estudio" i="format-list-numbers")
	Title(v-else :t="this.$route.params.titulo" s="Plan de estudio" i="format-list-numbers")
	Plan(facultad="fi" :carrera="carrera")

</template>
<script>

import Plan from '@/components/Plan.vue'
import Title from '@/components/Title.vue'

export default {
	name: 'plan',
	components: {
		Title,
		Plan
	},
	data() {
		return {
			carrera: this.$route.params.carrera
		}
	},
}

</script>